import Vue from "vue";
import Router from "vue-router";

Vue.use(Router);

const routes = [
  {
    path: '/',
    component: () => import("./../../src/layoutes/admin/AdminLayout.vue"),
    children: [
      {
        path: '',
        name: "Dashboard",
        component: () => import("../pages/Admin/Dashboard.vue"),
        meta: {
          requiresAuth: true
        }
      },

      {
        path: '/admin/user-data-list',
        name: "UserDataList",
        component: () => import("./../pages/Admin/UserDataList.vue")
      },
      {
        path: '/admin/user-add',
        name: "AddUser",
        component: () => import("./../components/Admin/AddUser.vue")
      },
      {
        path: '/admin/edit/user/:id',
        name: "EditUser",
        component: () => import("./../components/Admin/EditUser.vue")
      },
      {
        path: '/admin/user-services-list',
        name: "ServicesTable",
        component: () => import("./../pages/Admin/ServicesTable.vue")
      },
      {
        path: '/admin/add-services',
        name: "AddService",
        component: () => import("../components/Admin/AddService.vue")
      },
      {
        path: '/admin/view-services/:id',
        name: "ViewServices",
        component: () => import("../pages/Admin/ViewService.vue")
      },
      {
        path: '/admin/edit-services/:id',
        name: "EditServices",
        component: () => import("./../components/Admin/EditServices.vue")
      }
    ]
  },
  {
    path: '/login',
    name: "Adminlogin",
    component: () => import("./../components/Admin/Adminlogin.vue")
  },
  {
    path:'/admin/forgot/password',
    name:'ForgotPassword',
    component:() => import('./../components/Admin/ForgotPassword.vue')
  },
  {
    path:'/reset-password/:token',
    name:'ResetPassword',
    component:() => import('./../components/Admin/ResetPassword.vue')
  },
  // {
  //   path: "*",
  //   name: "ErrorPage",
  //   component: () => import("./../pages/Admin/ErrorPage.vue")
  // },
];

export default new Router({
 // mode: "history", // add this line to use HTML5 history mode
  routes
})


